import { Injectable, OnInit } from '@angular/core';
import { ModelService } from './model.service';
import { Todo } from './Todo';
import { BehaviorSubject } from 'rxjs';


@Injectable()
export class TasksService {
  constructor( private model: ModelService) {
    this.load();
   }

 

  /* Переменные для показа окон */
  showModalDelete: boolean = false;
  showModalEdit: boolean = false;

  

  textEdit: string;//переменная для input в modalEdit


  private items: Todo[] = []; // хранит обьекты
  private subj: BehaviorSubject<any> = new BehaviorSubject([]);

//возвращает поток
 get todos() {
   return this.subj.asObservable();
 }




//добавление todo
  addItem(text: string) {
    let todo: Todo = new Todo();
    todo.name = text;

    this.items.push(todo);
    this.subj.next(this.items);
    this.save();
  }

//удаление todo
  removeItem(index: number) {
    this.items.splice(index, 1);
    this.subj.next(this.items);
    this.save();
  }


//редакт текста в todo
  editText(index: number) {
    this.items[index].name = this.textEdit;
    this.subj.next(this.items);
    this.save();
  }

//увеличение приоритета на 1
  prioritetUp(index:number) {
    this.items[index].prioritet++;
    this.save();
  }
//понижение приоритета на 1
  prioritetDown(index:number) {
    this.items[index].prioritet--;
     if(this.items[index].prioritet <= 1 ) {
      this.items[index].prioritet = 1;
    }
    this.save();
  }


//изменение check на  false or true
  changeCheck(index: number) {
    this.items[index].check = !this.items[index].check;
    this.save();
  }

//Сортировка

//сортировка по check
sortCheck(check: boolean) {
  if(check) {
    this.items.sort((a,b) =>  a.check < b.check?1:-1 );
  } 
  else {
    this.items.sort((a,b) =>  a.check > b.check?1:-1 );
    
  }
  this.save();
}

//сортировка по приоритету
sortPrioritet(check: boolean) {
   if(check) {
  this.items.sort((a,b) =>  a.prioritet < b.prioritet?1:-1 );
  } else {
    this.items.sort((a,b) => a.prioritet > b.prioritet?1:-1 );
  }
  this.save();
}

// сортировка по дате
sortDate(check: boolean) {
   if(check) {
  this.items.sort((a,b) =>  a.date > b.date?1:-1 );
  } else {
    this.items.sort((a,b) => a.date < b.date?1:-1 );
  }
  this.save();
}


// пошук
search(text: string) {
 if(text == '') {
 this.load();
 } else {
    this.items = [...this.model.load()]
    .filter(item => {
        return  item.name.search(text) != -1;
    });
  this.subj.next(this.items);
 }

}






//обертки для сохранения и загрузки
   save() {
     this.model.save(this.items);
     this.subj.next(this.items);
   }

 
  load() {
    this.items = this.model.load();
    this.subj.next(this.items);
  }


















}
