import { Component} from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ModelService } from '../model.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent {

  constructor(private model: ModelService) { }

  errorEmail: boolean = false;
  errorPass: boolean = false;

  myForm : FormGroup = new FormGroup({
             
    "userPassw": new FormControl("", [Validators.required, this.validatorPass]),
    "userEmail": new FormControl("", [
                Validators.required, 
                Validators.email
    ])
});

validatorPass(pass: FormControl): {[s:string]:boolean} {
  const pattern = /[0-9]{8}/
  if(pattern.test(pass.value) == false || pass.value.length > 8) {
    return {'userPass': true};
  }
  if(pattern.test(pass.value)) {
    return null;
  }
}

onSubmit() {
  console.log(this.myForm);
  const rightEmail:string = 'testuser@todo.com';
  const rightPass:string = '12345678';

  if(this.myForm.controls['userPassw'].value == rightPass && this.myForm.controls['userEmail'].value == rightEmail) {
    this.model.showBlock = !this.model.showBlock;
  }
  
  }












}
