import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Todo } from './Todo';




@Injectable()
export class ModelService {
  showBlock: boolean = true;

  storage: any = window.localStorage;
  
  todos:any = []; // будет хранить данные из my-json-server

  readonly KEY: string = "ITEMS";

  constructor(private http: HttpClient) {
    
  }

  save(data: any) {
    let dataStr = JSON.stringify(data);
    this.storage.setItem(this.KEY, dataStr);
  }


  load() {
    if(!this.storage.getItem(this.KEY)) {
      this.getServerStorage();
      return this.todos;
    }
    return JSON.parse(this.storage.getItem(this.KEY));
  
  }

  //Функция получает данные и my-json-server  и добавляет в масив и сохраняет в локал стор
 async getServerStorage() {
      const task: Todo = new Todo();

      let get = await this.http.get("https://my-json-server.typicode.com/vladost99/vladost99.github.io/todos");
      
      const result = await get.subscribe((value: Todo[]) => {
        value.forEach(item => {
            task.name = item.name;
            task.check = item.check;
            task.date = item.date;
            task.prioritet = item.prioritet;

            this.todos.push(Object.assign({},task));
            this.storage.setItem(this.KEY, JSON.stringify(this.todos));
          });    
            
      });        
 }

 

















}